:root {
  --primary-font: "DM Sans", sans-serif;
  --primiry-text-color: #434455;
  --title-text-color: #2e2f42;
  --accent-color: #3997ff;
  --primary-bg-color: #f5f5f5;
  --white-color: #fff;
  --secondary-title-color: #f4f4fd;
  --secondary-text-color: #e7e9fc;
  --modal-color: #fcfcfc;
  --main-transition: 250ms cubic-bezier(.4, 0, .2, 1);
}

body {
  color: var(--primiry-text-color);
  margin: 0;
  padding: 0;
  font-style: normal;
}

img {
  display: block;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
}

[href] {
  text-decoration: none;
}

.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

